import React, { Component } from 'react';
import Trial from './Components/trial';
import './Css/app.css';

class App extends Component {
	constructor() {
		super();
		this.state = {
			search: 'Gel Zr!'
		};
	}
	
	updateSearch(event) {
		this.setState({search: event.target.value});
	}
	
  render() {
    return (
      <div className="App">
				<h1 className='title'>Caesars-Cipher Decoder</h1>
				<p className='description'>A Caesars-Cipher is a code where every letter in a sentance is shifted 13 letters down in the alphabet</p>
				<input type='text' value={this.state.search} onChange={this.updateSearch.bind(this)} className="cipherInput"/>
				<div className='cipherOutput'>
					<Trial cipher={this.state.search}/>
				</div>
      </div>
    );
  }
}

export default App;
